@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 62.5%;
  height: 100%;
  font-family: 'Manrope', sans-serif;
}
*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}

body {
  font-size: 1.8rem;
  height: 100%;
}

@layer components {
  .container {
    @apply w-[90%] max-w-[111rem] mx-auto;
  }
}
